<template>
  <div>
    <div v-if="inputInfo">
      <div class="container mt-3 col-9 col-sm-7 col-md-6 col-lg-5 col-xxl-4 border">
        <div class="mt-3 mb-3">
          <h1 class="h3 mb-3 fw-normal"> </h1>
          <div class="form-floating mb-2">
            <input type="text" class="form-control" id="floatingOrderId" placeholder="" @keyup.enter="submit" v-model="orderId">
            <label for="floatingOrderId"></label>
          </div>
          <div class="form-floating">
            <input type="text" class="form-control" id="floatingName" placeholder="" autocomplete="off" @keyup.enter="submit" v-bind:value="name" @input="inputName">
            <label for="floatingName"></label>
          </div>
          <button @click="submit" class=" btn btn-danger mt-3 mb-2 me-2" style="font-weight:bold;"></button>
          <button @click="home" class=" btn btn-success mt-3 mb-2" style="font-weight:bold;"></button>
        </div>
      </div>
    </div>

    <div v-if="orderInfo">
      <div class="container mt-3 col-9 col-sm-7 col-md-6 col-lg-5 col-xxl-4 border mb-3">
        <h3 class="mt-3 mb-3"> </h3>
        <hr/>
        <div class="row">
          <div class="col-4"> : </div>
          <div class="col-auto"> {{orderData.productName}} </div>
        </div>
        <hr/>
        <div class="row">
          <div class="col-4"> : </div>
          <div class="col-auto"> {{orderData.optionText}} </div>
        </div>
        <hr/>
        <div class="row">
          <div class="col-4">  : </div>
          <div class="col-auto"> {{orderData.orderNum}} </div>
        </div>
        <hr/>
        <div class="row">
          <div class="col-4">  : </div>
          <div class="col-auto"> {{orderData.totalPrice}} </div>
        </div>
        <hr/>
        <div class="row">
          <div class="col-4"> : </div>
          <div class="col-auto"> {{orderData.name}} </div>
        </div>
        <hr/>
        <div class="row">
          <div class="col-4">  : </div>
          <div class="col-auto" style="font-size: small;"> {{orderData.address}}, {{orderData.detailAddress}} </div>
        </div>
        <hr/>
        <div class="row">
          <div class="col-4">  : </div>
          <div class="col-auto"> {{orderData.currentState}} </div>
        </div>
        <hr/>
        <div class="row mb-3">
          <div class="col-4">  : </div>
          <div class="col-auto"> {{orderData.deliNum}} </div>
        </div>
      </div>
      <button @click="deliCheck" style="font-weight:bold;" class="btn btn-danger me-2"></button>
      <button @click="backPage" style="font-weight:bold;" class="btn btn-success"> </button>
    </div>
  </div>
  

</template>

<script>
import { ref } from 'vue';
import axios from 'axios';
import { useRouter } from 'vue-router';
export default {
  name:'NonMemberPage',
  setup() {
    const router = useRouter();
    let orderId = ref();
    let name = ref('');
    let orderData = ref({});
    let inputInfo = ref(true);
    let orderInfo = ref(false);

    function inputName(e) {
      name.value = e.target.value;
    }

    function submit() {
      if(orderId.value === '' || name.value === '') {
        alert('   ');
      } else {
        axios.get('/api/nonMemberOrder?orderId=' + orderId.value + '&name=' + name.value)
        .then(result=>{
          if(result.data === 'wrong') {
            alert('  .')
          } else {
            orderData.value = result.data;
            inputInfo.value = false;
            orderInfo.value = true;
          }
        })
        .catch(err=>{
          console.log(err);
        });
      }
    }

    function backPage() {
      inputInfo.value = true;
      orderInfo.value = false;
    }

    function home() {
      router.push({path : '/'});
    }

    function deliCheck() {
      let deliNum = orderData.value.deliNum;
      if(deliNum === 0) {
        alert('    .');
      } else {
        window.open('https://www.ilogen.com/m/personal/trace/' + deliNum);
      }
    }

    return {orderId, name, inputName, submit, orderData, inputInfo, orderInfo, home, backPage, deliCheck};
  }
}
</script>

