<template>
  <div>
    <div class="container">
      <nav>
        <div class="nav nav-tabs justify-content-center" id="nav-tab" role="tablist">
          <button @click="clickedTotal" class="nav-link check active" data-bs-toggle="tab" type="button" role="tab"  aria-selected="true"> </button>
          <button @click="clickedBrand" class="nav-link check" data-bs-toggle="tab" type="button" role="tab" aria-selected="false"></button>
          <button @click="clickedBrand" class="nav-link check" data-bs-toggle="tab" type="button" role="tab" aria-selected="false"></button>
          <button @click="clickedBrand" class="nav-link check" data-bs-toggle="tab" type="button" role="tab" aria-selected="false"></button>
          <button @click="clickedBrand" class="nav-link check" data-bs-toggle="tab" type="button" role="tab" aria-selected="false">PXG</button>
          <button @click="clickedBrand" class="nav-link check" data-bs-toggle="tab" type="button" role="tab" aria-selected="false"></button>
          <button @click="clickedBrand" class="nav-link check" data-bs-toggle="tab" type="button" role="tab" aria-selected="false"></button>
          <button @click="clickedBrand" class="nav-link check" data-bs-toggle="tab" type="button" role="tab" aria-selected="false"></button>
          <button @click="clickedBrand" class="nav-link check" data-bs-toggle="tab" type="button" role="tab" aria-selected="false"></button>
          <button @click="clickedBrand" class="nav-link check" data-bs-toggle="tab" type="button" role="tab" aria-selected="false"></button>
          <button @click="clickedBrand" class="nav-link check" data-bs-toggle="tab" type="button" role="tab" aria-selected="false"></button>
          
        </div>
      </nav>
    </div>

  <div class="container">
    <h3 class="mt-3 mb-3" style="text-align:left;">{{ korDivide }}</h3>
  </div>
  

    <div class="container">
      <div class="row row-cols-2 row-cols-sm-2 row-cols-md-3 row-cols-lg-3 row-cols-xl-4 g-4">
        <div class="col" v-for="item in products" :key="item">
          <div @click="clickCard" style="cursor:pointer;" class="card shadow-sm">
            <div id="img-border">
              <img id="image" :src='`${item.thumbnail}`' alt="logo" class="img-fluid img-thumbnail">
            </div>
            <div class="d-flex">
              <span class="ms-1" style="font-size:11px;"> : </span>
              <span class="ms-1" style="font-size:11px;">{{item.id}}</span>
              <span v-if="item.deliverOut" class="ms-2" style="font-size:11px; color:red; font-weight:bold;">[]</span>
            </div>
            
            <div class="card-body">
              
              <div id="text-border1" class="d-block d-sm-none">
                <p id="product-text" class="card-text" style="font-weight:600; font-size: 13px;"> {{item.productName}} </p>
              </div>
              <div id="text-border2" class="d-none d-sm-block">
                <p id="product-text" class="card-text" style="font-weight:600; font-size: 17px;"> {{item.productName}} </p>
              </div>
              
              <span class="text-decoration-line-through" style="font-weight:bold; color:gray">{{filter(item.beforeDiscount)}} </span>
              <div class="d-block d-sm-none">
                <span style="font-weight:900; font-size:24px; color:red;"> {{discount(item.beforeDiscount, item.productPrice)}}% </span>
                <span style="font-weight:900; font-size:18px;"> {{filter(item.productPrice)}}  </span>
              </div>
              <div class="d-none d-sm-block">
                <span style="font-weight:900; font-size:30px; color:red;"> {{discount(item.beforeDiscount, item.productPrice)}}% </span>
                <span style="font-weight:900; font-size:22px;"> {{filter(item.productPrice)}}  </span>
              </div>
            </div>
          </div>
        </div>
        
      </div>
    </div>
  </div>
  
  
</template>

<script>
import axios from 'axios'
import { ref } from '@vue/reactivity';
import { useRoute, useRouter } from 'vue-router';
export default {
  name:'ProductsPage',
  
  setup() {
    const router = useRouter();
    const route = useRoute();
    let products = ref([]);
    let korDivide = ref(route.query.korDivide);
    let engDivide = ref(route.query.engDivide);
    axios.get('/api/product/?engDivide=' + engDivide.value + '&korDivide=' + korDivide.value).then(res=>{
      
      products.value = res.data;
    }). catch(err=>{
      console.log(err);
    })

    function filter(val) {
      return String(val).replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    }

    function discount(before, after) {
      let answer = 100 * (1 - after / before);
      return Math.round(answer);
    }

    function clickedBrand(e) {
      let brandName = e.target.innerHTML;
      let brandEng;
      
      switch(brandName) {
        case '':
          brandEng = 'ping';
          break;
        case '':
          brandEng = 'callaway';
          break;
        case '':
          brandEng = 'taylormade';
          break;
        case 'PXG':
          brandEng = 'pxg';
          break;
        case '':
          brandEng = 'titleist';
          break;
        case '':
          brandEng = 'honma';
          break;
        case '':
          brandEng = 'xxio';
          break;
        case '':
          brandEng = 'yamaha';
          break;
        case '':
          brandEng = 'mizuno';
          break;  
        case '':
          brandEng = 'fourteen';
          break;
        case '':
          brandEng = 'majesty';
          break;
      }
      console.log(brandEng);
      axios.get('/api/product/brand?brandName=' + brandEng + '&divide=' + korDivide.value)
      .then(res=>{
        
        products.value = res.data;
      })
      .catch(err=>{
        console.log(err);
      })
    }

    function clickedTotal() {
      axios.get('/api/product/?korDivide=' + korDivide.value).then(res=>{
        
        products.value = res.data;
      }). catch(err=>{
        console.log(err);
      });
    }

    function purchase(e) {
      
      let productId = e.target.value;
      router.push({name : 'PurchasePage', params : {id : productId}});
    }

    function clickCard(e) {
      let productId = e.currentTarget.children[1].children[1].innerText;
      
      router.push({name : 'PurchasePage', params : {id : productId}});
    }
    return {products, filter, clickedBrand, clickedTotal, purchase, clickCard, discount, korDivide};

  }
}
</script>

