<template>
  
  <div class="container d-flex justify-content-center">
    <div style="width:80%;" class="border mt-5">
      
      <div style="font-size:large;" class="d-none d-sm-block mt-5 mb-3">
          .
      </div>
      <div style="font-size:medium;" class="d-block d-sm-none mt-5 mb-3">
          .
      </div>
      <div class="mb-3" v-if="login === false">
        <div style="font-size: small;"> : {{orderId}}</div> <div style="color:red; font-size: small;">(      .)</div>
      </div>
      <button @click="clickHome" style="font-weight: bold; font-size: large;" class="btn btn-danger me-1 mb-5 mt-2"></button>
      <button v-if="login" @click="clickPurchaseList" style="font-weight: bold; font-size: large;" class="btn btn-success ms-1 mb-5 mt-2"></button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { useRoute, useRouter } from 'vue-router'
import { ref } from 'vue';
export default {
  name:'PurchaseComplete',
  setup() {
    const router = useRouter();
    const route = useRoute();
    let nickName = ref('');
    let login = ref(true);
    let orderId = ref(route.query.orderId)
    axios.get('/api/product/submit')
    .then(result=>{
      if(result.data === 'need login') {
        login.value = false;
      } else nickName.value = result.data;
    })
    .catch(err=>{
      console.log(err);
    });

    function clickHome() {
      router.push({path : '/'});
    }
    function clickPurchaseList() {
      router.push({name : 'MyPage', query : {nickName : nickName.value, cart : 0}});
    }
    return {clickHome, clickPurchaseList, orderId, login};
  }
}
</script>

